@import url(https://fonts.googleapis.com/css2?family=Figtree:wght@300;400;500;700&display=swap);
body{margin:0;font-family:"Figtree",sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;min-height:100vh}#root{display:flex;width:100%;position:relative;min-height:100vh}.sidebar{width:200px;background:linear-gradient(45deg, #20a2e8, #0082c8, #0082c8, #20a2e8);color:#fff;position:fixed;top:0;bottom:0;border-right:1px solid #949494}.sidebar-list{list-style:none;padding:.25em;font-weight:300}.sidebar-list .active{font-weight:700}.sidebar-item{padding:.25em .5em;margin:.25em 0;cursor:pointer;border:1px solid rgba(0,0,0,0)}.sidebar-item:hover{background:rgba(255,255,255,.25);-webkit-backdrop-filter:blur(15x);backdrop-filter:blur(15x);box-shadow:0 4px 15px rgba(0,0,0,.3);border-radius:8px;border-color:rgba(255,255,255,.4)}.content{margin-left:160px;width:100%;display:flex;flex:1 1}
.flower{display:flex;flex:1 1;justify-content:center;align-items:center;background:#dce0f8;width:100%}.flower-area{display:flex;flex-direction:column;align-items:center}.flower-core{position:relative;display:flex;justify-content:center;align-items:center}.flower-top{margin-bottom:-30px;z-index:10}.leaf-right{position:absolute;left:4px}.leaf-right:hover{opacity:.6}.leaf-left{position:absolute;right:-2px}.leaf-left:hover{opacity:.6}
.container {
    background: rgb(9, 0, 20);
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    grid-gap: 200px;
    gap: 200px;
    width: 100%;
}

.container button {
    background: rgb(0, 0, 0);
    position: relative;
    display: inline-block;
    padding: 15px 30px;
    border: 2px solid #0f0;
    text-transform: uppercase;
    font-weight: 700;
    letter-spacing: 2px;
    color: white;
    -webkit-box-reflect: below 0px
        linear-gradient(to bottom, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.05));
    transition: 0.5s;
}

.container button:hover {
    color: rgb(113, 0, 0);
    box-shadow: 0 0 10px #0f0, 0 0 20px #0f0, 0 0 40px #0f0, 0 0 80px #0f0,
        0 0 120px #0f0;
}

.container button p {
    position: relative;
    z-index: 10;
}

.container button:nth-child(2) {
    -webkit-filter: hue-rotate(80deg);
            filter: hue-rotate(80deg);
}

.container button::before {
    content: '';
    position: absolute;
    left: -20px;
    top: 50%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    width: 20px;
    height: 2px;
    background: #0f0;
    box-shadow: 5px -8px 1px #0f0, 5px 8px 1px #0f0;
    transition: width 0.5s, left 0.5s, height 0.5s, box-shadow 0.5s;
    transition-delay: 1s, 0.5s, 0s, 0s;
}

.container button:hover::before {
    width: 55%;
    height: 100%;
    left: -2px;
    box-shadow: 0px -2px 1px #0f0, 0px 2px 1px #0f0;
    transition-delay: 0s, 0s, 0.5s, 1s;
}

.container button::after {
    content: '';
    position: absolute;
    right: -20px;
    top: 50%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    width: 20px;
    height: 2px;
    background: #0f0;
    box-shadow: -5px -8px 1px #0f0, -5px 8px 1px #0f0;
    transition: width 0.5s, left 0.5s, height 0.5s, box-shadow 0.5s;
    transition-delay: 1s, 0.5s, 0s, 0s;
}

.container button:hover::after {
    width: 55%;
    height: 100%;
    right: -2px;
    box-shadow: 0px -2px 1px #0f0, 0px 2px 1px #0f0;
    transition-delay: 0s, 0s, 0.5s, 1s;
}

